html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Jost", sans-serif;
  background-color: var(--color-white);
  font-weight: 500;
  color: var(--color-dark);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 40px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main-link {
  position: relative;
  font-size: 15px;
  color: var(--color-dark);
}

.main-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  opacity: 0;
  background-color: currentColor;
  transition: opacity 0.2s;
}

.main-link--white {
  color: var(--color-white);
}

.main-link:hover::before {
  opacity: 1;
}

.container-narrow {
  max-width: 1406px;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 36px;
  padding-bottom: 36px;
}

.header__shop-nav {
  margin-left: 250px;
}

.logo {
  display: inline-block;
  width: 117px;
  height: 22px;
}

.logo svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-dark);
  object-fit: cover;
}

.catalog-header {
  padding-top: 38px;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 45px;
}

.nav__link {
  position: relative;
  font-size: 15px;
}

.nav__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  opacity: 0;
  background-color: var(--color-dark);
  transition: opacity 0.2s;
}

.nav__link:hover::before {
  opacity: 1;
}

.nav__link--current::before {
  opacity: 1;
}

.shop-nav {
  display: flex;
  align-items: center;
}

.shop-nav__link {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.shop-nav__link--user {
  background-image: url("../../img/user.png");
}

.shop-nav__item:not(:last-child) {
  margin-right: 25px;
}

.cart {
  width: auto;
  height: auto;
}

.cart__icon {
  background-image: url("../../img/cart.png");
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.search-form__btn {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../../img/search.png");
}

.quantity {
  display: inline-flex;
  align-items: center;
  padding: 0 5px;
  height: 16px;
  border-radius: 25px;
  font-size: 10px;
  font-weight: 700;
  color: var(--color-white);
  background-color: var(--color-dark);
}

.hero {
  position: relative;
  height: 100vh;
  height: 1080px;
  min-height: 700px;
  background-color: var(--quil-gray);
}

.hero::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(var(--color-black), 0.05);
}

.hero__container {
  position: relative;
  height: 100%;
}

.hero__title {
  margin: 0;
  margin-bottom: 40px;
  max-width: 540px;
  font-size: 72px;
  font-weight: 700;
}

.hero__descr {
  margin: 0;
  font-size: 18px;
}

.hero__image {
  position: absolute;
  right: 262px;
  bottom: 0;
  max-height: 90%;
}

.hero__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 260px;
  height: 100%;
}

.banner-slider {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.banner-slider .swiper-slide {
  height: 100%;
}

.banner-pag {
  bottom: 45px !important;
}

.banner-pag .swiper-pagination-bullet {
  margin: 0 12px !important;
  width: 6px;
  height: 6px;
  background-color: var(--color-white);
  opacity: 0.5;
}

.banner-pag .swiper-pagination-bullet-active {
  position: relative;
  background-color: var(--color-dark);
  opacity: 1;
}

.banner-pag .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  z-index: -1;
  border-radius: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  background-color: var(--color-white);
}

.marketing {
  position: absolute;
  z-index: 10;
  display: flex;
  left: 40px;
  bottom: 50px;
  padding: 10px;
  width: 280px;
  background-color: var(--color-white);
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  transform: translateX(-150vw);
  transition: transform 0.5s ease-out;
}

.marketing--visible {
  transform: translateX(0);
  transition: transform 0.5s ease-out;
}

.marketing__text {
  margin-left: 10px;
}

.marketing__image {
  flex-shrink: 0;
}

.marketing__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.marketing__descr {
  color: var(--color-gray);
  display: block;
  font-size: 11px;
}

.marketing__title {
  display: block;
  font-size: 15px;
  margin: 0;
  margin-bottom: 7px;
  min-height: 34px;
  padding-right: 20px;
}

.marketing__when-from {
  display: block;
  font-size: 11px;
  padding-right: 30px;
}

.marketing__btn {
  position: absolute;
  cursor: pointer;
  right: 10px;
  padding: 0;
  top: 10px;
  width: 10px;
  height: 10px;
  background-image: url("../../img/close.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.banners__container {
  padding-top: 50px;
  padding-bottom: 30px;
}

.banners__list {
  display: flex;
}

.banners__item {
  width: 33%;
}

.banners__item:not(:last-child) {
  margin-right: 1%;
}

.banners-article {
  padding: 54px 60px;
  padding-right: 50%;
  min-height: 290px;
  background-color: #e9e4df;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.banners-article-midseasons {
  background-image: url("../../img/art1.jpg");
}

.banners-article-summer {
  background-image: url("../../img/art2.jpg");
}

.banners-article-accs {
  background-image: url("../../img/art3.jpg");
}

.banners-article__title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 36px;
}

.main-products__container {
  padding-top: 70px;
  padding-bottom: 30px;
}

.main-products__nav {
  display: flex;
  justify-content: center;
  margin-bottom: 46px;
}

.main-products__item {
  margin-right: 30px;
}

.main-products__btn {
  font-size: 24px;
}

.main-products__btn--current::before {
  opacity: 1;
}

.main-products__btn-center {
  text-align: center;
}

.main-products__more {
  font-size: 13px;
  font-weight: 500;
  color: var(--color-gray);
  margin-top: 95px;
  display: inline-flex;
  align-items: center;
  flex-direction: column;
}

.main-products__more span {
  display: inline-block;
  margin-bottom: 12px;
}

.main-products__more svg {
  width: 12px;
  height: 21px;
  fill: var(--color-dark);
}

.products-grid {
  display: flex;
  flex-wrap: wrap;
}

.products-grid__item {
  margin-right: 30px;
  margin-bottom: 80px;
}

.products-grid__item:nth-child(5n) {
  margin-right: 0;
}

.products-grid__item:nth-last-child(-n + 5) {
  margin-bottom: 0;
}

.product {
  position: relative;
}

.product__image {
  margin-bottom: 20px;
}

.product__image img {
  max-height: 100%;
  object-fit: cover;
}

.product__title {
  margin: 0;
  margin-bottom: 8px;
  font-size: 13px;
  font-weight: 500;
  color: var(--color-tundora);
}

.product__title a::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
}

.product__price {
  font-size: 13px;
  font-weight: 700;
}

.shop-banners {
  padding-top: 75px;
}

.shop-banners__container {
  padding: 0;
  display: flex;
  justify-content: space-between;
}

.shop-banner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  padding: 160px 20px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-dark);
  color: var(--color-white);
}

.shop-banner::before {
  content: "";
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
  top: 0;
  background-color: rgba(215, 8, 8, 0.1);
}

.shop-banner__title {
  position: relative;
  z-index: 1;
  margin: 0;
  margin-bottom: 20px;
  font-size: 48px;
  font-weight: 500;
}

.shop-banner__text {
  position: relative;
  z-index: 1;
  font-size: 15px;
}

.shop-banner--man {
  background-image: url("../../img/banner1.jpg");
}

.shop-banner--woman {
  background-image: url("../../img/banner2.jpg");
}

.hero-catalog {
  padding-top: 130px;
}

.hero-catalog__slider {
  overflow: hidden;
  background: #f8f8f8;
  width: 100%;
}

.catalog-slide {
  padding: 108px;
}

.catalog-slide__title {
  margin: 0;
  margin-bottom: 16px;
  max-width: 375px;
  font-size: 36px;
  font-weight: 700;
  color: var(--color-dark);
}

.catalog-slide__descr {
  max-width: 390px;
  margin-bottom: 40px;
  font-size: 15px;
  line-height: 28px;
  color: var(--color-tundora);
}

.catalog-slide__btns {
  display: flex;
  align-items: center;
}

.catalog-slide__link {
  display: inline-block;
  margin-right: 11px;
  padding: 16px 45px;
  font-size: 13px;
}

.catalog-slide__link--dark {
  border: 1px solid var(--color-dark);
  color: var(--color-white);
  background: var(--color-dark);
}

.catalog-slide__link--light {
  border: 1px solid var(--color-dark);
  color: var(--color-dark);
  background: transparent;
}

.breadcrumbs__list {
  display: flex;
  align-items: center;
  padding: 37px 0;
  padding-top: 36px;
  padding-bottom: 28px;
  border-bottom: 1px solid #eee;
}

.breadcrumbs__item {
  position: relative;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 35px;
}

.breadcrumbs__item:not(:last-child)::after {
  content: "/";
  position: absolute;
  right: -20px;
  top: 50%;
  transform: translateY(-50%);
}

.breadcrumbs__link {
  color: var(--color-gray);
  text-transform: uppercase;
  font-size: 10px;
}

.breadcrumbs__link--current {
  color: var(--color-dark);
}

.catalog-filters {
  width: 309px;
  padding-top: 24px;
  padding-right: 65px;
  margin-right: 30px;
}

.catalog-content {
  min-height: 500px;
}

.catalog-content .container {
  display: flex;
}

.hide-filters {
  padding-left: 24px;
  margin-bottom: 18px;
  color: var(--color-dark);
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  background-image: url("../../img/cross.png");
  background-position: left center;
  background-repeat: no-repeat;
}

.catalog-filter {
  border-top: 1px solid #eee;
}

.catalog-filter--open .catalog-filter__bottom {
  display: block;
}

.catalog-filter--open .catalog-filter__toggle::before {
  display: none;
}

.catalog-filter__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 34px;
  padding-bottom: 34px;
  cursor: pointer;
  user-select: none;
}

.catalog-filter__caption {
  display: flex;
  align-items: center;
}

.catalog-filter__title {
  display: inline-block;
  margin: 0;
  color: var(--color-dark);
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
}

.catalog-filter__quantity {
  min-width: 16px;
  margin-left: 5px;
  transform: translateY(-1px);
}

.catalog-filter__bottom {
  display: none;
  padding-bottom: 34px;
}

.catalog-filter__toggle {
  position: relative;
  width: 12px;
  height: 12px;
  color: #aaa;
}

.catalog-filter__toggle::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  width: 2px;
  height: 100%;
  background-color: currentColor;
  transform: translateX(-50%);
}

.catalog-filter__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  transform: translateY(-50%);
}

.catalog-filter__items {
  columns: 2;
}

.catalog-filter__item {
  margin-bottom: 13px;
}

.catalog-filter__item:nth-child(5n) {
  margin-bottom: 0;
}

.catalog-filter:last-child {
  border-bottom: 1px solid #eee;
}

.custom-checkbox {
  display: inline-block;
  cursor: pointer;
  user-select: none;
}

.custom-checkbox__text {
  position: relative;
  padding-left: 15px;
  font-size: 13px;
  color: var(--color-gray);
}

.custom-checkbox__text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 3px;
  width: 11px;
  height: 11px;
  border: 2px solid #aaa;
}

.custom-checkbox__text::after {
  content: "\2713";
  position: absolute;
  left: 2px;
  top: 0;
  color: #aaa;
}

.custom-checkbox__input {
  display: none;
}

.custom-checkbox__input:checked + span {
  color: var(--color-dark);
}

.custom-checkbox__input:checked + span::before {
  border-color: var(--color-dark);
}

.custom-checkbox__input:checked + span::after {
  color: var(--color-dark);
}

.catalog-grid {
  width: 987px;
  padding-top: 28px;
}

.catalog-grid__content {
  display: flex;
  flex-wrap: wrap;
}

.catalog-grid__item {
  margin-bottom: 79px;
