.catalog-grid [data-grid-columns="3"] .catalog-grid__item {
  width: 31.3%;
}

.catalog-grid [data-grid-columns="3"] .catalog-grid__item:not(:nth-child(3n)) {
  margin-right: 3%;
}

.catalog-grid [data-grid-columns="4"] .catalog-grid__item {
  width: 22.7%;
}

.catalog-grid [data-grid-columns="4"] .catalog-grid__item:not(:nth-child(4n)) {
  margin-right: 3%;
}

.catalog-grid [data-grid-columns="5"] .catalog-grid__item {
  width: 17.6%;
}

.catalog-grid [data-grid-columns="5"] .catalog-grid__item:not(:nth-child(5n)) {
  margin-right: 3%;
