  border-radius: 12px;
  background-color: #eee;
  transition: background-color 0.3s, color 0.3s;
}

.catalog-choise__item svg {
  margin-left: 5px;
  width: 8px;
  height: 9px;
  fill: currentColor;
  pointer-events: none;
