.catalog-filters {
  width: 309px;
  padding-top: 24px;
  padding-right: 65px;
  margin-right: 30px;
}

.catalog-content {
  min-height: 500px;

  .container {
    display: flex;
  }
}

.hide-filters {
  padding-left: 24px;
  margin-bottom: 18px;

  color: var(--color-dark);
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  background-image: url("../../img/cross.png");
  background-position: left center;
  background-repeat: no-repeat;
}

.catalog-filter {
  border-top: 1px solid #eee;

  &--open {
    .catalog-filter__bottom {
      display: block;
    }

    .catalog-filter__toggle {
      &::before {
        display: none;
      }
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 34px;
    padding-bottom: 34px;

    cursor: pointer;
    user-select: none;
  }

  &__caption {
    display: flex;
    align-items: center;
  }

  &__title {
    display: inline-block;
    margin: 0;

    color: var(--color-dark);
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
  }

  &__quantity {
    min-width: 16px;
    margin-left: 5px;

    transform: translateY(-1px);
  }

  &__bottom {
    display: none;
    padding-bottom: 34px;
  }

  &__toggle {
    position: relative;
    width: 12px;
    height: 12px;

    color: #aaa;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      width: 2px;
      height: 100%;

      background-color: currentColor;

      transform: translateX(-50%);
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: 2px;

      background-color: currentColor;

      transform: translateY(-50%);
    }
  }

  &__items {
    columns: 2;
  }

  &__item {
    margin-bottom: 13px;

    &:nth-child(5n) {
      margin-bottom: 0;
    }
  }

  &:last-child {
    border-bottom: 1px solid #eee;
  }
}

.custom-checkbox {
  display: inline-block;
  cursor: pointer;
  user-select: none;

  &__text {
    position: relative;
    padding-left: 15px;

    font-size: 13px;
    color: var(--color-gray);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 3px;
      width: 11px;
      height: 11px;
      border: 2px solid #aaa;
    }

    &::after {
      content: "\2713";
      position: absolute;
      left: 2px;
      top: 0;
      color: #aaa;
    }
  }

  &__input {
    display: none;

    &:checked + span {
      color: var(--color-dark);

      &::before {
        border-color: var(--color-dark);
      }

      &::after {
        color: var(--color-dark);
      }
    }
  }
}

.catalog-grid {
  width: 987px;
  padding-top: 28px;

  &__content {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    margin-bottom: 79px;
  }

  [data-grid-columns="3"] {
    .catalog-grid__item {
      width: 31.3%;

      &:not(:nth-child(3n)) {
        margin-right: 3%;
      }
    }
  }

  [data-grid-columns="4"] {
    .catalog-grid__item {
      width: 22.7%;

      &:not(:nth-child(4n)) {
        margin-right: 3%;
      }
    }
  }

  [data-grid-columns="5"] {
    .catalog-grid__item {
      width: 17.6%;

      &:not(:nth-child(5n)) {
        margin-right: 3%;
      }
    }
  }
}

.catalog-props {
  margin-bottom: 15px;
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 27px;

    select {
      display: none;
    }
  }
}

.catalog-columns {
  display: flex;
  align-items: center;

  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;

  &__item {
    &:not(:last-child) {
      margin-right: 7px;
    }
  }

  &__text {
    margin-right: 3px;
  }

  &__list {
    display: flex;
    align-items: center;
  }

  &__btn {
    padding: 0 5px;

    font-size: 10px;
    color: var(--color-gray);

    &:hover {
      color: var(--color-dark);
    }

    &--current {
      color: var(--color-dark);

      &::before {
        opacity: 1;
      }
    }
  }
}

.catalog-choise {
  font-size: 0;
  display: none;

  &__item {
    margin-right: 5px;
    margin-bottom: 5px;
    padding: 5px 12px;

    color: var(--color-tundora);
    font-size: 13px;

    border-radius: 12px;
    background-color: #eee;

    transition: background-color 0.3s, color 0.3s;

    svg {
      margin-left: 5px;
      width: 8px;
      height: 9px;
      fill: currentColor;
      pointer-events: none;
    }

    &:hover {
      color: var(--color-white);
      background-color: var(--color-dark);
    }
  }

  &__clear {
    position: relative;

    font-size: 13px;
    color: var(--color-dark);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -5px;
      width: 100%;
      height: 2px;
      opacity: 1;

      background-color: currentColor;

      transition: opacity 0.2s;
    }
  }
}
